                                <div class="page-image" v-if="!item[3] && !item[4]" :style="'background-image: url('+i1+')'"></div>
                                <div class="page-text" v-html="item[2]" style="overflow: auto;">
                                </div>
                                <div class="page-footer">
                                    {{index + basePage + 1}}
